.container {
  display: flex;
  flex-direction: column;
  padding: 8px 25px 16px 25px;
  border-top: 1px solid #e6ecf0 ;
  border-bottom: 10px solid #e6ecf0;
  font-family: "Noto Sans TC";
}
.input-body {
  display: flex;
  .user-avatar{
    .avatar-img {
      width: 50px;
      height: 50px;
      border-radius: 100px;
    }
  }
}
.input-textarea {
  width: 100%;
  margin-left: 8px;
  resize: none;
  border: transparent;
  outline: none;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: var(--color-secondary);
}
.footer {
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  .error-message {
    margin-right: 20px;
    color: var(--color-error);
    font-size: 15px;
    line-height: 15px;
    font-weight: 500;
  }
}