.container {
  width: 100%;
  display: grid;
  grid-template-columns: minmax(280px, 2.33fr) minmax(400px, 5.657fr) minmax(
      308px,
      4.011fr
    );
  grid-auto-rows: 100vh;
  font-family: "Noto Sans TC";
}

.column1 {
  grid-column: 1/2;
  grid-row: 1/2;
}

.column-2 {
  grid-column: 2/3;
  grid-row: 1/2;
}

.column-2-container {
  border: 1px solid #e6ecf0;
  width: 100%;
  height: 100%;
}

.title {
  line-height: 26px;
  margin: 24px 0 24px 24px;
  font-weight: 700;
}

.auth-form {
  padding: 24px 24px 0 24px;
  border-top: 1px solid #e6ecf0;
}

.authinput-group {
  width: 100%;
}

.save-button {
  width: 14.8%;
  float: right;
}

.column-3 {
  grid-column: 3/4;
  grid-row: 1/2;
}

// SweetALert

:global(.swal2-title) {
  font-size: 28px !important;
}
